@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Kalam&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  letter-spacing: 2px; }

html {
  scroll-behavior: smooth; }

body {
  margin: 0px;
  width: 100vw;
  text-align: center;
  background-color: #ecf0f1;
  color: #63707e; }

.header {
  position: fixed;
  z-index: 1;
  background: #ecf0f100;
  top: 0;
  -webkit-transition: top 0.2s ease-in-out;
  transition: top 0.2s ease-in-out;
  display: flex;
  width: 100vw;
  justify-content: space-between;
  align-items: center; }
  .header .mylogo {
    width: 40px;
    margin: 10px 20px 10px 20px; }
  .header .header-link {
    margin: 0 30px 0 30px;
    text-decoration: none;
    color: #34495e; }
  .header .header-link:hover {
    text-decoration: underline; }

h1 {
  margin: 0;
  font-size: 5.625rem;
  color: #34495e;
  font-family: 'Kalam', cursive; }

h2 {
  font-size: 2rem;
  color: #34495e;
  font-weight: bold;
  margin: 0 auto;
  font-family: 'Kalam', cursive;
  margin-bottom: 15px; }

h3 {
  color: #3498db; }

hr {
  border-style: dotted;
  border-width: 10px 0px 0px 0px;
  margin: 30px auto;
  width: 6%;
  border-color: #63707e; }

.skills {
  margin: 40px; }

p {
  line-height: 2; }

.intro {
  display: flex;
  background: url(/aboutme/static/media/me.351ba1da.jpg) no-repeat;
  background-size: cover;
  position: relative;
  height: 100vh;
  width: 100%; }
  .intro h1 {
    justify-self: flex-start;
    margin-left: 20px;
    margin-top: 70px; }
  .intro h2 {
    margin-right: 120px;
    margin-top: 50vh;
    justify-self: flex-end; }

.title h1 {
  margin: 0px; }

.title .description {
  width: 50%;
  margin: auto;
  padding-bottom: 40px; }

.title .button {
  margin-top: 200px; }

.footer {
  background-color: #f1c40f;
  padding-top: 10px;
  margin-top: 50px; }

.skill-row-o {
  width: 50%;
  margin: 60px auto;
  text-align: left;
  line-height: 2; }

.skill-row-e {
  width: 50%;
  margin: 120px auto;
  text-align: left;
  line-height: 2; }

.profImg {
  margin-top: 30px;
  margin-bottom: 30px;
  width: 200px;
  background-color: #f1c40f;
  border-radius: 20px; }

.contact-description {
  width: 50%;
  margin: 30px auto; }

.button {
  border-radius: 26px;
  box-shadow: 2px 2px 3px #a3a3a3;
  color: #ffffff;
  font-size: 20px;
  background: #3498db;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
  margin-bottom: 10px; }

.button:hover {
  background: #c56868;
  text-decoration: none; }

.copyright {
  color: black;
  margin-bottom: 0;
  font-size: 0.75rem; }

.logo {
  width: 30px;
  margin: 5px; }

.skillImg {
  width: 200px;
  height: 200px;
  float: left;
  margin: 20px; }

@media screen and (max-width: 750px) {
  .intro {
    background: url(/aboutme/static/media/memobile.9e7c3caa.jpeg) no-repeat;
    background-size: cover;
    width: 100vw; }
    .intro h1, .intro h2 {
      color: #34495e00; }
  p {
    text-align: justify; }
  .description, .skill-row-e, .skill-row-o {
    display: flex;
    flex-direction: column;
    width: 50%; }
  .skillImg {
    float: none; } }

